import { createMuiTheme } from "../../node_modules/@material-ui/core";

// A theme with custom primary and secondary color.
// It's optional.
export const theme = createMuiTheme({
    palette: {
        text:{
            primary:"#1A212C"
        },
        // 배경
        background: {
            default:"#ffffff",
            paper:"#ffffff",
        },
        primary: {
            light: '#5896e3',
            main: '#1069b1',
            dark: '#003f81',
            contrastText: '#ffffff',
        }
    },
    typography:{        
        title:{
            color : "#D13F32",            
        },
        // boardlist에서 보여주는 title
        body1 :{
            color : "#ffffff",
        },
        body2 :{
            color : "#000000"
        },
        display1:{
            fontSize : 50
        }
    },
    overrides: {
        MuiInputLabel: {
            root: {
                color: "#ffffff"
            }
        },
        MuiInputAdornment: {
            root: {
                color: "#ffffff"
            }
        },
        // 게시글 제목
        MuiExpansionPanelSummary:{
            root:{
                backgroundColor : "#1069B1"
            },
            
        },
        // 게시글 요약
        MuiExpansionPanelDetails:{
            root:{
                backgroundColor :"#B1BAC0", 
            }
        },
        MuiListItemText :{
            primary : {
                color: "#1A212C"
            }
        }
    }
});


