import * as React from 'react';
import { StyleRulesCallback, Theme, withStyles } from '../../../node_modules/@material-ui/core';

const style: StyleRulesCallback = (theme: Theme) => ({
    RightContainer: {
        flexBasis : 25+"%"
    }
})

interface IProps {
    classes: {
        RightContainer: string;
    }
}

class RightContainer extends React.Component<IProps> {
    public render() {
        return (
            <div className = {this.props.classes.RightContainer}>
                test<br />
                test<br />
                test<br />
                test<br />
                test<br />
                test<br />
                test<br />
                test<br />
                test<br />
                test<br />
                test<br />
                test<br />
                test<br />
                test<br />
            </div>
        )
    }
}

export default withStyles(style)(RightContainer);


