import * as React from 'react';
import { StyleRulesCallback, Theme, withStyles, Typography, Divider } from '@material-ui/core';
import IBoardModel from '../../../Constance/BoardModel';
const style: StyleRulesCallback = (theme: Theme) => ({
    list: {
        width: 100 + "%",
    },
    heading: {
        fontSize: theme.typography.pxToRem(15),
        fontWeight: theme.typography.fontWeightRegular,
    },
})

interface IProps {
    classes: {
        list: string;
        heading: string;
    },
}
interface IState {
    boardList: IBoardModel[];
    openPanel: number;
}

class IntroPage extends React.Component<IProps, IState> {
    constructor(props: IProps) {
        super(props);
        this.state = {
            boardList: [],
            openPanel: -1
        }
        this.handlePanel = this.handlePanel.bind(this);
        this.openSummary = this.openSummary.bind(this);
        this.closeSummary = this.closeSummary.bind(this);
    }

    public render() {
        return (
            <div style={{
                width: 100 + "%"
            }}>
                <Typography variant="display1">IntroPage</Typography>
                <Divider />
            </div>
        );
    }
    private handlePanel(num: number) {
        if (this.state.openPanel === num) {
            this.setState({ openPanel: -1 });
        } else {
            this.setState({ openPanel: num });

        }
    }
    private openSummary(num: number) {
        this.setState({
            openPanel: num
        });
    }
    private closeSummary() {
        this.setState({
            openPanel: -1
        });
    }
}

export default withStyles(style)(IntroPage);


